// src/components/common/TransactionList.js
import React from 'react';
import exportpdf from '../../services/exportpdf';
import exportService from '../../services/exportService';
const TransactionList = ({ transactions, onDelete, onEdit }) => {
    const handleExportData = () => {
        exportpdf.exportToPDF(transactions);

    };
    const handleDelete = (transactionId) => {

        onDelete(transactionId);

    };

    const handleEdit = (transactionId) => {

        onEdit(transactionId);
    };
    const exportToCSV = () => {
        exportService.exportToCSV(transactions);
    }

    return (
        <div className="card card-flush h-xl-100">

            <div className="card-header pt-5">

                <h3 className="card-title align-items-start flex-column">
                    <span className="card-label fw-bold text-gray-800">TRANSACTION TABLE</span>

                    <span className="text-gray-400 pt-1 fw-semibold fs-6">Counted in Rupees</span>
                </h3>



                <div className="card-toolbar">
                    <div>
                        <p onClick={exportToCSV} className="btn btn-sm btn-light">CSV Report</p>
                        <br />

                        <p onClick={handleExportData} className="btn btn-sm btn-light">PDF Report</p>
                    </div>
                </div>


            </div>



            <div className="card-body py-3">

                <div className="table-responsive">

                    <table className="table table-row-dashed align-middle gs-0 gy-4">

                        <thead>
                            <tr className="fs-7 fw-bold border-0 text-gray-400">
                                <th className="min-w-150px" >DATE</th>
                                <th className="min-w-150px " >DESCRIPTION</th>
                                <th className="min-w-150px " >CATEGORY</th>
                                <th className="min-w-150px ">AMOUNT</th>
                                <th className="min-w-150px ">TYPE</th>
                                <th className="min-w-150px  ">ACTIONS</th>



                            </tr>
                        </thead>



                        <tbody> {transactions.map((transaction) => (
                            <tr key={transaction.id}>
                                <td className="" >
                                    <div className="">
                                        <span className="text-gray-800 fw-bold fs-6 me-1">{transaction.date}</span>


                                    </div>
                                </td>
                                <td className="" >
                                    <p className="text-gray-800 fw-bold text-hover-primary mb-1 fs-6">{transaction.description}</p>
                                </td>
                                <td className="" >
                                    <p className="text-gray-800 fw-bold text-hover-primary mb-1 fs-6">{transaction.category}</p>
                                </td>



                                <td className="" >
                                    <div className="d-flex ">
                                        {transaction.type === 'expense' ? (
                                            <span className="text-danger min-w-60px d-block text-end fw-bold fs-6">- ₹{transaction.amount}</span>
                                        ) : (
                                            <span className="text-success min-w-60px d-block text-end fw-bold fs-6">+ ₹{transaction.amount}</span>
                                        )}

                                    </div>
                                </td>
                                <td className="" >
                                    <p className="text-gray-800 fw-bold text-hover-primary mb-1 fs-6">{transaction.type}</p>
                                </td>
                                <td className="">
                                    <p onClick={() => handleEdit(transaction.id)} className="btn btn-icon btn-bg-light btn-active-color-primary btn-sm me-1">

                                        <span className="svg-icon svg-icon-3"><svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <path opacity="0.3" d="M21.4 8.35303L19.241 10.511L13.485 4.755L15.643 2.59595C16.0248 2.21423 16.5426 1.99988 17.0825 1.99988C17.6224 1.99988 18.1402 2.21423 18.522 2.59595L21.4 5.474C21.7817 5.85581 21.9962 6.37355 21.9962 6.91345C21.9962 7.45335 21.7817 7.97122 21.4 8.35303ZM3.68699 21.932L9.88699 19.865L4.13099 14.109L2.06399 20.309C1.98815 20.5354 1.97703 20.7787 2.03189 21.0111C2.08674 21.2436 2.2054 21.4561 2.37449 21.6248C2.54359 21.7934 2.75641 21.9115 2.989 21.9658C3.22158 22.0201 3.4647 22.0084 3.69099 21.932H3.68699Z" fill="currentColor" />
                                            <path d="M5.574 21.3L3.692 21.928C3.46591 22.0032 3.22334 22.0141 2.99144 21.9594C2.75954 21.9046 2.54744 21.7864 2.3789 21.6179C2.21036 21.4495 2.09202 21.2375 2.03711 21.0056C1.9822 20.7737 1.99289 20.5312 2.06799 20.3051L2.696 18.422L5.574 21.3ZM4.13499 14.105L9.891 19.861L19.245 10.507L13.489 4.75098L4.13499 14.105Z" fill="currentColor" />
                                        </svg>
                                        </span>
                                    </p>
                                    <p onClick={() => handleDelete(transaction.id)} className="btn btn-icon btn-bg-light btn-active-color-primary btn-sm">

                                        <span className="svg-icon svg-icon-3"><svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M5 9C5 8.44772 5.44772 8 6 8H18C18.5523 8 19 8.44772 19 9V18C19 19.6569 17.6569 21 16 21H8C6.34315 21 5 19.6569 5 18V9Z" fill="currentColor" />
                                            <path opacity="0.5" d="M5 5C5 4.44772 5.44772 4 6 4H18C18.5523 4 19 4.44772 19 5V5C19 5.55228 18.5523 6 18 6H6C5.44772 6 5 5.55228 5 5V5Z" fill="currentColor" />
                                            <path opacity="0.5" d="M9 4C9 3.44772 9.44772 3 10 3H14C14.5523 3 15 3.44772 15 4V4H9V4Z" fill="currentColor" />
                                        </svg>
                                        </span>
                                    </p>
                                </td>
                            </tr>
                        ))}
                        </tbody>

                    </table>

                </div>

            </div>

        </div>
    );
};

export default TransactionList;
